@import url("basic.css");

.section-container {
    transition: all 0.5s;
    /* background-color: var(--bs-dark); */
    /* background-image: linear-gradient(0deg, #6495edb3, rgba(255, 127, 80, 0.7)); */
}

.section-plate {
    transition: all 0.5s;
}

.main-content {
    position: relative;
}

.main-content > .btn {
    background-color: var(--peach-dark);
    color: var(--bs-light);
    border-radius: 25px 0px 25px 0px;
    padding: 10px 25px;
    transition: all 0.5s;
}

.main-content > .btn:hover {
    background-color: var(--dark-blue);
    color: var(--bs-light);
    border-radius: 25px;
}

.main-content > img {
    aspect-ratio: 1 / 1;
}

.gallery-info::first-letter {
    color: var(--peach-dark);
    font-size: 25px;
}

.gallery-info {
    font-size: 18px;
    text-align: justify;
    text-align-last: center;
}

.slider-header {
    text-transform: uppercase;
    color: var(--peach-dark);
}

.cluster img {
    opacity: 0%;
}