@import url("./basic.css");

@keyframes zoomOutBack {
    from {
        opacity: 1;
        transform: scale(1.1);
    } 
    50% {
        opacity: 0.95;
    }
    to {
        opacity: 0.9;
        transform: scale(1);
    }
}

.intro-header {
    transition: all 0.3s;
}

.intro-div-pic {
    background: linear-gradient(0deg, #6495edb3, rgba(255, 127, 80, 0.7)), url("../images/misc/collage.png");
    background-size: contain;
    background-position: 50% 0%;
    background-repeat: repeat;
    backdrop-filter: blur(2px);
    opacity: 0.9;
    animation: 1s zoomOutBack ease-in-out forwards;
    transition: all 0.5s;
}

.intro-div-pic > div:first-child {
    animation: 0.8s fadeIn ease-out forwards 1s;
}

.intro-div-pic > div:first-child > h1 {
    font-size: 8vw;
}

.intro-div-pic > img:first-child {
    filter: blur(1px) grayscale(1);
    width: min(50%, 350px);
    width: 350px;
}

.scroll-gif {
    position: absolute;
    width: clamp(30px, 15%, 70px) !important;
    top: 85%;
    left: 47%;
}

.my-basic-info { 
    position: absolute;
    top: 40vh;
    display: block;
}

.my-basic-info > div {
    font-size: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif, "Calibri", "Roboto";
}

.my-name { font-size: 50px; color: var(--sec); }

.about-me-p::first-letter { font-size: 25px; color: var(--peach-dark); }

.about-me-p {
    text-align: justify;
    font-size: 18px;
    color: var(--bs-dark);
}

.about-me {
    background-color: white;
    border-radius: 25px;
    position: relative;
}

#About {
    overflow: hidden;
}

.edu-content {
    text-align-last: center;
    text-align: justify;
}

.edu-content::first-letter { font-size: 50px; color: var(--peach-dark);}
.education-box {margin-bottom: 100px; transition: all 0.3s;}

.year-bulb {
    height: 5px;
    background-color: var(--peach-light);
    display: block;
    position: relative;
    margin: 20px auto;
    cursor: pointer;
    transition: all 0.3s;
}

.year-bulb:hover {
    background-color: var(--peach-dark) !important;
}

.year-bulb::before {
    content: '';
    position: absolute;
    color: white;
    padding: 10px;
    border-radius: 50%;
    background-color: var(--sec);
    transform: translateX(-20px) translateY(-8px);
    cursor: pointer;
}

.year-bulb.active::after {
    content: '';
    height: 0px;
    width: 0px;
    position: absolute;
    transform: translateX(6.5vw) translateY(-10px);
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-top: 15px solid transparent;
    border-bottom: 20px solid var(--peach-dark);
}

.year-bulb-last.active::after {
    display: none;
}

.year-bulb-last {
    background-color: transparent;
    width: 0px !important;
    padding: 0px;
}

.year-bulb.active {
    background-color: var(--peach-dark);
}

.card {
    border: 0px !important;
    min-width: 25%;
}

.exp-card-body {
    border-radius: 25px;
    position: relative;
    overflow: hidden;
}

.exp {
    padding-bottom: 50px;
}

.exp-img {
    width: 200px;
    position: absolute;
    top: 40%;
    right: -10%;
}

.exp-num {
    font-size: 75px;
    color: var(--sec);
}

.exp-num::after {
    content: ' + ';
    font-size: 50px;
    vertical-align: middle;
    color: grey;
}

.left-below {
    width: 60%;
    border-radius: 25px;
    height: 500px;
    background:  linear-gradient(210deg, var(--light-blue) 50%, var(--peach));
    position: absolute;
    transform: rotateZ(150deg);
    left: -45%;
    opacity: 75%;
    top: 40%;
    animation: 1.3s design-triangle ease-in-out forwards;
    z-index: -1;
}

.right-below {
    width: 60%;
    border-radius: 25px;
    height: 500px;
    background-image: linear-gradient(45deg, var(--blue), var(--peach));
    position: absolute;
    transform: rotateZ(210deg);
    left: 75%;
    opacity: 25%;
    top: 70%;
    animation: 1.3s design-triangle ease-in-out forwards;
    z-index: -1;
}

.left-above {
    width: 50%;
    border-radius: 25px;
    height: 400px;
    background-color: var(--light-blue);
    position: absolute;
    transform: rotateZ(135deg);
    opacity: 75%;
    left: -35%;
    top: 65%;
    animation: 1.3s design-triangle ease-in-out forwards;
    z-index: -1;
}

.right-above {
    width: 50%;
    border-radius: 25px;
    height: 400px;
    background-color: var(--light-blue);
    opacity: 50%;
    position: absolute;
    transform: rotateZ(225deg);
    left: 75%;
    top: 85%;
    animation: 1.3s design-triangle ease-in-out forwards;
    z-index: -1;
}
