@import url("./basic.css");

.project-logos {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.project-logo-img {
    /* filter: grayscale(1) contrast(112.5%); */
    transition: all 0.5s;
    border-color: lightgrey;
    cursor: pointer;
}

.line {
    height: 100px;
    background-color: lightgrey;
    width: 2px;
    margin: 0px auto;
    transform: translateX(-50%);
    content: "";
    animation: 0.5s lineLoader ease-out forwards;
}

@keyframes lineLoader {
    from {
        transform: scaleY(0);
    } to {
        transform: scaleY(1);
    }
}

.project-dropper {
    transition: all 0.5s;
}

#projects {
    background-color: var(--light-grey);
    scroll-behavior: smooth;
}

#current-project {
    position: relative;
    min-height: 100vh;
    max-height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

#current-project > .btn {
    z-index: 5;
}

[class*="current-slide"] {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: 0.5s fadeIn ease-out forwards;
}

.current-project-img {
    width: clamp(200px, 50%, 500px);
}

.current-project > .btn, a:hover {
    cursor: pointer;
}

.current-project-tech {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (max-width: 576px) {
    .current-slide > p { font-size: medium !important; }
}


.carousel .carousel-indicators [data-bs-target]{
    border-top: none;
    border-bottom: none;
    height: 10px;
    width: 10px;
    transition: all 0.3s;
    border-radius: 50%;
}

.carousel .carousel-indicators .active{ background-color: var(--peach-dark); width: 35px !important; border-radius: 15px !important; }

.carousel .carousel-card {
    height: 400px;
    max-height: 400px;
    overflow-y: auto;
    box-shadow: 10px 10px 20px lightgrey;
    position: relative;
}

::-webkit-scrollbar { width: 0px; background: transparent; }

.slide-info {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 35px;
    height: 35px;
}

.slide-info > img {
    padding: 5px !important;
}

.slide-info:hover {
    background-color: #EEE;
    border-radius: 50%;
    cursor: pointer;
}

@media screen and (min-width: 992px) {
    .carousel-card {
        text-align: justify;
    }

    .carousel-card img { height: 100% !important; }
}

@media screen and (max-width: 700px) {
    .carousel-card {
        width: 100% !important;
    }
}

@media screen and (max-width: 992px) {
    .carousel-card {
        width: 75% !important;
    }
}

@media screen and (max-width: 556px) {
    .carousel-card {
        width: 100% !important;
        font-size: 13px;
        padding: 15px;
        text-align: justify;
    }
    .carousel-card img {
        height: auto;
    }
}

.project-box {
    position: relative;
}

.project-desc {
    display: flex;
    flex-direction: row;
    align-self: center;
    text-align: justify;
    text-align-last: center;
}

.heading-underline {
    position: relative;
    display: inline-block;
    width: fit-content;
}

.heading-underline::after {
    position: absolute;
    width: 80%;
    height: 4px;
    background: linear-gradient(90deg, var(--peach-dark), var(--light-blue));
    content: '';
    top: 100%;
    left: 10%;
    transform: translateX(-50%);
    border-radius: 50px;
    animation: 0.5s heading-underliner ease-out forwards;
}

@keyframes heading-underliner {
    from {
        transform: scaleX(0);
        opacity: 0;
    } to {
        transform: scaleX(1);
        opacity: 1;
    }
}

.project-indicator {
    position: fixed;
    border: 2px solid lightgrey;
    border-radius: 10px 0px 0px 10px;
    width: 50px;
    height: fit-content;
    right: 25px;
    top: 30%;
    z-index: 1;
}

.project-indicator-circle {
    position: relative;
    width: 30px;
    height: 30px;
    display: block;
    margin: 20px auto;
    border-radius: 50%;
    background-color: var(--peach-dark);
    cursor: pointer;
    transition: all 0.3s;
}

.project-indicator-circle:hover {
    background-color: var(--light-blue);
}

.project-indicator-circle.active {
    background-color: var(--blue);
}

.project-indicator-circle::after {
    position: absolute;
    height: 20px;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    content: '';
    background-color: var(--peach-dark);
    width: 5px;
}

.project-indicator-circle:last-child::after {
    display: none;
}

.project-tech-img-container {
    display: flex;
    flex-direction: row;
    align-self: center;
    align-content: flex-end;
    justify-content: center;
}

.project-tech-img {
    width: 30px;
    height: 30px;
    /* border-radius: 50%; */
    margin: 5px 10px;
}

.project-link {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 20px;
    cursor: pointer;
    padding: 10px;
    transition: all 0.3s;
    border-radius: 50%;
    text-decoration: none;
}

.project-link:hover {
    background-color: rgba(255, 255, 255, 0.25);
}

.project-tech-img[src*="Django"] {
    filter: invert(1);
}

div[id*="project"] {
    border-radius: 10px;
}

#project-info::first-letter {
    color: var(--peach-dark);
    font-size: 25px;
}

#project-info {
    font-size: 18px;
    text-align: justify;
}