@import url(https://db.onlinewebfonts.com/c/9f94dc20bb2a09c15241d3a880b7ad01?family=Menlo);

.skills-table > img {
    max-width: 100px;
    max-height: 100px;
    margin: 15px 20px;
}

.skills-table {
    flex-wrap: wrap;
    padding-bottom: 50px;
}

#Skills {
    background-color: var(--bs-light);
    left: -100%;
    top: 0%;
    margin-left: auto;
    margin-right: auto;
    z-index: 5;
}

.branch-top-middle {
    min-height: 150px;
    background-color: lightgrey;
    width: 2px;
    position: relative;
    left: 50%;
    z-index: 1;
}

.branch-start {
    min-height: 150px;
    background-color: lightgrey;
    width: 2px;
    position: relative;
    left: 50%;
    z-index: 1;
}

.branch-left-start {
    left: 25%;
}

.branch-middle-start {
    left: 50%;
}

.branch-right-start {
    left: 75%;
}

.branch-start::before {
    min-height: 15px;
    width: 15px;
    content: '';
    position: absolute;
    top: -15px;
    left: -300%;
    background-color: var(--bs-light);
    border: 2px solid lightgrey;
    border-radius: 50%;
    z-index: 1;
}

.image {
    position: absolute;
    left: 50%;
    transform: translate(-25px, -25px);
    z-index: 2;
    background-color: var(--bs-light);
}

.left-image {
    left: 16.75%;
}

.middle-image {
    left: 50%;
}

.right-image {
    left: 83.5%;
}

.branch-start-left-start {
    height: 150px;
    background-color: transparent;
    width: 16.67%;
    position: absolute;
    left: 16.75%;
    top: 150px;
    border-radius: 100% 0% 0% 0%;
    border-top: 3px solid lightgrey;
    border-left: 3px solid lightgrey;
}

.branch-start-left-end {
    height: 153px;
    background-color: transparent;
    width: 16.67%;
    position: absolute;
    left: calc(16.75% + 16.67%);
    top: 0px;
    border-radius: 0% 0% 100% 0%;
    border-bottom: 3px solid lightgrey;
    border-right: 3px solid lightgrey;
}

.branch-end-left-start {
    height: 150px;
    background-color: transparent;
    width: 17.25%;
    position: absolute;
    right: calc(16.75% - 4px);
    top: 150px;
    border-radius: 0% 100% 0% 0%;
    border-top: 3px solid lightgrey;
    border-right: 3px solid lightgrey;
}

.branch-end-left-end {
    height: 103px;
    background-color: transparent;
    width: 16.67%;
    position: absolute;
    right: calc(16.75% + 16.67% - 1px);
    top: 50px;
    border-radius: 0% 0% 0% 100%;
    border-bottom: 3px solid lightgrey;
    border-left: 3px solid lightgrey;
}

@keyframes coverHeight {
    from {
        transform: translateY(-100px);
        opacity: 0%;
    }
    50% {
        opacity: 0.75;
    } to {
        transform: translateY(0px);
        opacity: 100%;
    }
}

.typer {
    min-height: 40px;
    border-radius: 15px;
    position: relative;
    border: 1px solid lightgrey;
    background-color: var(--bs-light);
}

.typer-header {
    min-height: 40px;
    background-color: lightgrey;
    border-bottom: 2px solid lightgrey;
    border-radius: 15px 15px 0px 0px;
}

.typer-body {
    top: 40px;
    width: 100%;
    min-height: 300px;
    border-radius: 0px 0px 15px 15px;
    padding: 10px 15px;
    font-family: 'IBM Plex Mono', monospace !important;
}

.typer-body > p {
    font-size: 16px;
}

[class^="dot"] {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: inline;
    margin-top: 1%;
    margin-left: 1%;
}

.dot-red {
    background-color: #FF605C;
}

.dot-yellow {
    background-color: #FFBD44;
}

.dot-green {
    background-color: #00CA4E;
}

.dot-holder {
    display: flex;
    width: clamp(calc(100% - 25px), 90%, calc(100% - 50px));
}

.tooltip {
    background-color: lightgrey;
    border: 1px solid lightgrey;
    border-radius: 5px;
    min-height: 25px;
    min-width: 50px;
    position: absolute;
    opacity: 1 !important;
    text-align: center;
    padding: 5px 15px;
    transform: translateX(-50%);
}

.tooltip-bottom {
    top: 20%;
}

.tooltip-bottom::before {
    background-color: transparent;
    content: '';
    width: 15px;
    height: 15px;
    position: absolute;
    top: 100%;
    left: calc(50% - 9px);
    border-top: 12px solid lightgrey;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-bottom: 12px solid transparent;
    opacity: 1 !important;
}

.tooltip-top {
    top: 14%;
}

.tooltip-top::before {
    background-color: transparent;
    content: '';
    width: 15px;
    height: 15px;
    position: absolute;
    top: -80%;
    left: calc(50% - 9px);
    border-bottom: 12px solid lightgrey;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 12px solid transparent;
    opacity: 1 !important;
}

.coconut {
    position: absolute;
    right: 0%;
    top: 5%;
}

#skills-info::first-letter {
    color: var(--peach-dark);
    font-size: 25px;
}

#skills-info {
    font-size: 18px;
}